<template>
  <v-data-iterator :items="drinks" :items-per-page.sync="itemsPerPage">
    <template v-slot:header>
      <v-toolbar flat color="white">
        <v-toolbar-title>Crud Atma Pedia</v-toolbar-title>

        <v-spacer></v-spacer>
        <v-dialog v-model="dialog" max-width="500px">
          
          <template v-slot:activator="{ on }">
            <v-btn color="primary" dark class="mb-2" v-on="on">buat minuman</v-btn>
          </template>

          <v-card>
            <v-card-title>
              <span class="headline">{{ formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-row>

                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.name" label="Drinks name"></v-text-field>
                  </v-col>

                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.calories" label="Calories"></v-text-field>
                  </v-col>

                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.fat" label="Fat (g)"></v-text-field>
                  </v-col>

                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.carbs" label="Carbs (g)"></v-text-field>
                  </v-col>

                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.protein" label="Protein (g)"></v-text-field>
                  </v-col>

                </v-row>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>

              <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
              <v-btn color="blue darken-1" text @click="save">Save</v-btn>

            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>

    <template v-slot:default="props">
      <v-row>
        <v-col v-for="item in props.items" :key="item.name" cols="12" sm="6" md="4" lg="3">
            <v-card>
              <v-card-subtitle>MINUMAN</v-card-subtitle>
              <v-divider></v-divider>
              <v-card-title class="font-weight-bold">{{ item.name }}</v-card-title>
            
              <v-list dense>
                <v-list-item>
                  <v-list-item-content>Calories</v-list-item-content>
                  <v-list-item-content class="align-end">: {{ item.calories }}</v-list-item-content>
                </v-list-item>

                <v-list-item>
                  <v-list-item-content>Fat</v-list-item-content>
                  <v-list-item-content class="align-end">: {{ item.fat }}</v-list-item-content>
                </v-list-item>

                <v-list-item>
                  <v-list-item-content>Carbs</v-list-item-content>
                  <v-list-item-content class="align-end">: {{ item.carbs }}</v-list-item-content>
                </v-list-item>

                <v-list-item>
                  <v-list-item-content>Protein</v-list-item-content>
                  <v-list-item-content class="align-end">: {{ item.protein }}</v-list-item-content>
                </v-list-item>
              </v-list>

              <v-card-actions>
                <v-btn color="blue" class="mb-2" @click="editItem(item)">EDIT</v-btn>
                <v-btn color="red" class="mb-2" @click="deleteItem(item)">DELETE</v-btn>
              </v-card-actions>
            </v-card>
          </v-col>
        </v-row>
      </template>
  </v-data-iterator>
</template>

<script>
  import { drinksRef } from '../firebase';

  export default {

    data: () => ({
      search : '',
      cek : -1,
      dialog : false,

      itemsPerPage: 4,

      drinks: [],

      editedIndex: -1,

      editedItem: {
        name: '',
        calories: 0,
        fat: 0,
        carbs: 0,
        protein: 0,
      },

      defaultItem: {
        name: '',
        calories: 0,
        fat: 0,
        carbs: 0,
        protein: 0,
      },

    }),
    
    firebase: {
      drinks : drinksRef
    },

    computed: {
      formTitle() {
        return this.cek === -1 ? 'New Item' : 'Edit Item'
      },
    },

    watch: {
      dialog(val) {
        val || this.close()
      },
    },

    methods: {
      close() {
        this.dialog = false
      },

      // methods untuk save
      save() {
        if (this.cek > -1) {
          drinksRef.child(this.editedIndex).set({
            name : this.editedItem.name, 
            calories : this.editedItem.calories,
            fat : this.editedItem.fat, 
            carbs : this.editedItem.carbs,
            protein : this.editedItem.protein})
          this.clear()
        } else {
          drinksRef.push({
            name : this.editedItem.name, 
            calories : this.editedItem.calories,
            fat : this.editedItem.fat, 
            carbs : this.editedItem.carbs,
            protein : this.editedItem.protein})
          this.clear()
        }
        this.close()
      },

      clear(){
        this.editedItem = {}
        this.editedItem.name = ''
        this.editedItem.calories = 0
        this.editedItem.fat = 0
        this.editedItem.carbs = 0
        this.editedItem.protein = 0
        this.cek = -1
      },

      editItem(item) {
        this.editedIndex = item['.key']
        this.cek = 0
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem(item) {
        confirm('Are you sure you want to delete this item?') && drinksRef.child((item['.key'])).remove()
      },
    },
  }
</script>